:root {
  --clr-cream: #A59678;
  --clr-dark-blue: #3B4050;
  --clr-gray: #6F737F;
  --clr-dark: #2E323F;
  --clr-white: #FFFFFF;
  /* font-sizes */
  --fs-900: rem(90);
  --fs-800: rem(36);
  --fs-700: rem(30);
  --fs-600: rem(24)
  --fs-500: rem(21);
  --fs-400: rem(18);
  --fs-300: rem(16);
  --fs-200: rem(14);
  /* font-families */
  --ff-carter: "Carter One", cursive;
  --ff-oswald: "Oswald", sans-serif;
  --ff-montserrat: "Montserrat", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-carter);
}

.flex {
  display: flex;
}